@import url(https://fonts.googleapis.com/css2?family=Play:wght@400;700&display=swap);
:root {
    --color-turquoise: #00D4FF;
    --color-coral: #ed7947;
    --color-dimgray: #4c5d67;
    --color-darkslategray: #37474f;
    --color-black: #0b1f34;
}

body,
html {
    padding: 0;
    margin: 0;
}

* {
    font-family: 'Play', sans-serif;
}

body {
    background: #0b1f34;
    background: var(--color-black);
}

a {
    text-decoration: none;
}

.text-primary {
    color: #00D4FF;
    color: var(--color-turquoise);
}

.text-secondary {
    color: #ed7947;
    color: var(--color-coral);
}

button {
    border: none;
    cursor: pointer;
}

button:focus {
    outline: none;
}
.home-container {
  margin-top: 150px;
  display: flex;
  justify-content: center;
}

.home-text {
  width: 490px;
  margin-right: 70px;
}

.home-text-title {
  font-size: 55px;
  color: var(--color-turquoise);
}

.home-text-subtitle {
  font-size: 24px;
  color: var(--color-coral);
}

.home-actions {
  display: flex;
  height: 70px;
  margin-top: 150px;
}

.home-btn {
  background-color: var(--color-turquoise);
  border-radius: 10px 0px 0px 10px;
  color: var(--color-black);
  font-weight: bold;
  font-size: 24px;
  padding: 20px 45px;
}

.home-btn:hover {
  background-color: #04c7ef;
}

.home-btn-icon {
  border-radius: 0px 10px 10px 0px;
  width: 70px;
  background-color: var(--color-coral);
  display: flex;
  align-items: center;
  justify-content: center;
}

/* responsive */

@media (max-width: 1250px) {
  .home-image {
    max-width: 400px;
    height: auto;
  }
}

@media (max-width: 1250px) {
  .home-image {
    max-width: 400px;
    height: auto;
  }

  .home-text-title {
    font-size: 45px;
  }
  
  .home-text-subtitle {
    font-size: 20px;
  }

  .home-text {
    width: 410px;
    margin-right: 20px;
  }

  .home-btn {
    font-size: 18px;
    padding: 10px 25px;
  }

  .home-actions {
    margin-top: 50px;
  }
}

@media (max-width: 850px) {
  .home-container {
    margin-top: 50px;
  }

  .home-actions {
    height: 50px;
    justify-content: center;
  }

  .home-image {
    display: none;
  }

  .home-text {
    margin-right: 0;
    text-align: center;
    padding: 0 10px;
  }
}
.page-container {
  width: 80%;
  margin: 0 auto;
  margin-bottom: 50px;
  padding-top: 50px;
}

.filters-container.records-actions {
  justify-content: flex-end;
}
 
.records-table {
  width: 100%;
  border-bottom: 10px solid var(--color-turquoise);
  border-radius: 0 0 10px 10px;
}

.records-table thead {
  background-color: var(--color-turquoise);
  text-align: left;
  color: #0E1B28;
  border-radius: 10px;
}

.records-table thead tr {
  height: 50px;
}

.records-table thead th:first-child {
  padding-left: 50px;
  border-top-left-radius: 10px;
}

.records-table thead th:last-child {
  border-top-right-radius: 10px;
}

.records-table tbody td:first-child {
  padding-left: 50px;
}

.records-table tbody tr {
  height: 45px;
  color: #FFF;
  font-size: 18px;
}

.records-table tbody tr:nth-child(odd) {
  background-color: var(--color-darkslategray);
}

.records-table tbody tr:nth-child(even) {
  background-color: var(--color-dimgray);
}

.filters-container {
  background-color: var(--color-darkslategray);
  display: flex;
  justify-content: space-between;
  padding: 13px 15px;
  margin-bottom: 25px;
  border-radius: 10px;
}

.filters-container input {
  height: 44px;
  font-weight: bold;
  font-size: 16px;
  color: #9E9E9E;
  border: 1px solid #E1E1E1;
  border-radius: 10px;
  box-sizing: border-box;
  margin-right: 20px;
  padding-left: 15px;
}

.filters-container input:focus {
  outline: none;
}

.filters-container button {
  font-weight: bold;
  font-size: 16px;
  border-radius: 10px;
  padding: 13px 60px;
}

.filters-container .clean-filters {
  background-color: var(--color-coral);
  color: #FFF;
  margin-left: 20px;
}

.filters-container .clean-filters:hover {
  background-color: #e07243;
}

.filters-container .action-filters {
  background-color: var(--color-turquoise);
  color: #0E1B28;
}

.filters-container .action-filters:hover {
  background-color: #04c7ef;
}
.pagination-container {
  margin-top: 30px;
  text-align: center;
}

.pagination-item {
  width: 40px;
  height: 40px;
  border-radius: 10px;
  margin-right: 10px;
  font-weight: bold;
  font-size: 18px;
}

.pagination-item.active {
  background: var(--color-turquoise);
}

.pagination-item.inactive {
  background: var(--color-darkslategray);
  color: var(--color-coral);
}

.pagination-item.active:hover {
  background-color: #04c7ef;
}

.pagination-item.inactive:hover {
  background-color: #29353b;
}
.main-header {
  height: 30px;
  padding: 20px 40px;
  display: flex;
  background-color: var(--color-darkslategray);
}

.logo-text {
  font-weight: bold;
  font-size: 24px;
  margin-left: 15px;
}

.logo-text-1 {
  color: var(--color-coral);
}

.logo-text-2 {
  color: #FFF;
}

@media (max-width: 850px) {
  .main-header {
    justify-content: center;
  }
}
.chart-container {
  display: flex;
}

.top-related {
  background-color: var(--color-dimgray);
  padding-top: 30px;
  padding-left: 70px;
  border-radius: 10px;
  flex: 1 1;
}

.top-related-title {
  font-size: 36px;
  color: #FFF;
}

.games-container {
  display: flex;
  justify-content: center;
}

.games-list {
  width: 100%;
}

.game-result {
  display: flex;
  align-items: center;
}

.game-title {
  font-size: 24px;
  color: var(--color-turquoise);
  margin-right: 40px;
  width: 400px;
  text-align: right;
}

.game-rating {
  height: 20px;
  width: 300px;
  background-color: var(--color-coral);
  border-radius: 10px;
}

.charts {
  color: #FFF;
  width: 400px;
  margin-left: 30px;
}

.chart-title {
  font-size: 28px;
}

.platform-chart {
  padding-top: 15px;
  padding-left: 25px;
  height: 373px;
  margin-bottom: 25px;
  background-color: var(--color-dimgray);
  border-radius: 10px;
}

.gender-chart {
  padding-top: 15px;
  padding-left: 25px;
  height: 373px;
  background-color: var(--color-dimgray);
  border-radius: 10px;
}

